.searchicon {
    color: rgba(0,0,0,0.3);
    vertical-align: middle;
    margin-top: -3px;
    margin-right: 6px;
}

.searchBar {
    background-color: #d43c31;
    padding: 5px;
    border-radius: 5px;
}

.searchField{
    width: 140px;
    -webkit-transition: width .20s ease-in-out;
    -o-transition: width .20s ease-in-out;
    transition: width .20s ease-in-out;
}

.MuiInput-focused-93{
    width: 180px;
    -webkit-transition: width .20s ease-in-out;
    -o-transition: width .20s ease-in-out;
    transition: width .20s ease-in-out;
}


